___CSS_LOADER_EXPORT___.push([module.id, `/* ESTRUCTURA PRINCIPAL WEB */

/* Contenedor principal */
.container {
  width: 90%;
  max-width: 1200px;
  margin: 0 auto;
  padding-top: 50px;
  padding-bottom: 100px;
}

/* Layout principal */
main {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin: 20px 0;
  flex: 1;
  background: white;
}

article {
  flex: 1 1 60%;
  padding: 20px;
  border-radius: 5px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

/* Sidebar */
aside {
  flex: 1 1 35%;
  background: white;
  padding: 20px;
  border-radius: 5px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

/* Footer */
footer {
  background: #333;
  color: white;
  text-align: center;
  padding: 10px;
  margin-top: auto;
}

/* Responsive */
@media (max-width: 768px) {
  main {
    flex-direction: column;
  }

  article,
  aside {
    width: 100%;
    margin-bottom: 20px;
  }
}
